/* WordSearch.css */

/* Container for the entire word search game */
.word-search-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f9fafb;
  font-family: Arial, sans-serif;
  padding: 20px;
}

/* Grid container styles */
.grid-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  background-color: #ffffff;
  padding: 20px;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Grid styles */
.grid {
  display: grid;
  grid-template-columns: repeat(25, 40px);
  gap: 5px;
}

.grid-cell {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: bold;
  color: #374151;
  background-color: #ffffff;
  border: 2px solid #d1d5db;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s, transform 0.2s;
}

.grid-cell:hover {
  background-color: #f3f4f6;
  transform: scale(1.05);
}

.grid-cell.selected {
  background-color: #60a5fa;
  color: #ffffff;
  border-color: #3b82f6;
}

.grid-cell.found {
  background-color: #444;
  color: #ffffff;
  border-color: #333;
}

/* Words to find section */
.words-to-find {
  width: 100%;
  max-width: 600px;
  padding: 20px;
  background-color: #ffffff;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.words-to-find h3 {
  margin-bottom: 16px;
  font-size: 20px;
  font-weight: bold;
  color: #111827;
}

.words-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}

.word-item {
  font-size: 16px;
  font-weight: bold;
  padding: 8px 12px;
  background-color: #f3f4f6;
  color: #374151;
  border: 2px solid #d1d5db;
  border-radius: 4px;
}

.word-item.found {
  text-decoration: line-through;
  color: #9ca3af;
  background-color: #e5e7eb;
}

/* Popup message when all words are found */
.popup {
  margin-top: 20px;
  padding: 20px;
  background-color: #d1fae5;
  border: 2px solid #10b981;
  border-radius: 8px;
  text-align: center;
}

.popup h3 {
  font-size: 20px;
  font-weight: bold;
  color: #047857;
  margin-bottom: 8px;
}

.popup p {
  font-size: 16px;
  color: #065f46;
}

